.example {  
  padding-top: 30px;
  padding-bottom: 40px;

  background-color: #f2f2f2;
   
  .example__wrapper {
    width: 280px;
    padding: 0 20px;
  }

  .example__price {
    font-family: 'Oswald', sans-serif;
    font-size: 14px;
    line-height: 20px;
    font-weight: 400;
    text-transform: uppercase;
  }

  .before-after {
    width: 320px;
    margin-left: -20px;
  }

  .before-after__label {
    font-size: 16px;
    line-height: 20px;
    font-weight: 500;
    text-transform: uppercase;
  }
}

.example__details {
  span {
    font-family: 'Oswald', sans-serif;
    font-size: 24px;
    line-height: 24px;
  }

  small {
    font-size: 12px;
    line-height: 12px;
  }
 
}